<template>
  <div class="htmlbacc" ref="htmlbacc">
    <div class="container">
      <!-- <div class="bigbox"> -->
      <nav style="margin-top: 40px">
        <div
          class="iconfont icon-fanhui"
          @click="back"
          style="font-size: 23px"
        ></div>
        <div class="cententbx" style="color: rgba(255, 255, 255, 0.5)">
          这首歌很好听？试试分享给好友吧！

          <div
            class="zhizhen"
            v-show="dbshow"
            :class="{ playyes: playing, playno: !playing }"
          ></div>
        </div>
        <div
          class="iconfont icon-fenxiang"
          style="font-size: 23px"
          @click="fenxiangshow = true"
        ></div>
      </nav>

      <div class="fenxiang" v-show="fenxiangshow">
        <div>{{ titleText }}</div>
        <div>
          当前链接:
          <span>
            http://localhost:8080/#/bofangqi?id={{ AudioplaysongId }}</span
          >
        </div>

        <div>
          <span @click="ctrlC">复制到剪贴板</span
          ><span @click="fenxiangshow = false">取消</span>
        </div>
      </div>
      <div
        class="fenxiangzhezhao"
        v-show="fenxiangshow"
        @click="fenxiangshow = false"
      ></div>

      <div class="qiehuan">
        <!-- 专辑 -->
        <div class="lyricbox" v-show="dbshow">
          <div
            class="songimg anima"
            :class="{ playing: playing }"
            @click.stop="qie"
          >
            <div class="imgbor">
              <img :src="song?.al?.picUrl" alt="" style="width: 100%" />
              <!-- <img src= alt=""> -->
            </div>
          </div>
        </div>

        <!-- 歌词 -->
        <div class="playsonglyeis" v-show="dbshow">{{ playsonglyeis }}</div>
        <div v-show="!dbshow" style="position: relative">
          <div
            class="lyricbox2"
            ref="gecibox"
            :scrollTop="gecitop"
            @touchstart="anxia"
            @touchend="sonkai"
            :class="{ playing: playing }"
          >
            <div
              class="song"
              v-for="(item, index) in geci"
              :key="index"
              ref="lyricElm"
              :class="{ altt: index == songlyricIndex }"
              @click="genggai(item.time)"
            >
              <!-- {{ item.text }} -->
              <span
                :style="{
                  animationDuration:
                    ((geci[index + 1]?.time || songoverTime) - item.time) * 1 +
                    's',
                }"
                >{{ item.text }}</span
              >
            </div>
          </div>
          <!-- <div class="zhezhao"></div> -->
          <!-- <div class="zhezhao2"></div> -->
        </div>
      </div>

      <!-- 控制条 -->
      <div class="songTitle">
        <div class="leftbox">
          <span class="title">{{ song?.name }}</span>
          <span> {{ song?.ar[0]?.name }}</span>
        </div>
        <div class="rightbox">
          <div class="ShouCang" style="visibility: hidden">
            <img src="../assets/Shoucang.png" alt="" />
          </div>
          <div class="Pinglun" style="visibility: hidden"></div>
        </div>
      </div>
      <div class="BoFangic">
        <div class="time">
          <span v-if="!songoldTime">00:00</span>
          <span>{{ songoldTime | TimesetMS }}</span>
          <span>{{ songoverTime | TimesetMS }}</span>
          <input
            type="range"
            style="width: 100%"
            min="0"
            :max="songoverTime"
            v-model.number="userValue"
            @input="inputing = true"
            @change="
              inputing = false;
              $emit('changeSongTime', userValue);
            "
          />
        </div>
        <div class="btnbox">
          <div
            class="iconfont icon-23_shunxubofang"
            style="font-size: 30px; visibility: hidden"
          ></div>

          <div
            class="iconfont icon-shangshou"
            @click="$emit('Previoussong')"
            style="font-size: 30px"
          ></div>

          <div
            class="iconfont icon-bofang"
            v-show="!playing"
            @click.stop="$emit('playsong')"
            style="font-size: 60px"
          ></div>

          <div
            class="iconfont icon-zanting"
            v-show="playing"
            @click.stop="$emit('pauessong')"
            style="font-size: 60px"
          ></div>

          <div
            class="iconfont icon-xiashou"
            @click="$emit('nextsong')"
            style="font-size: 30px"
          ></div>

          <div
            class="iconfont icon-yinlebofangliebiao"
            @click="liebiaoqiehuan"
            style="font-size: 25px"
          ></div>
        </div>
      </div>

      <!-- <audio controls :src="`https://music.163.com/song/media/outer/url?id=${$route.query.id}.mp3`"></audio> -->
      <!-- </div> -->

      <!-- 列表 -->
      <div
        class="zhezhao songList"
        v-if="listShow"
        @click="listShow = false"
      ></div>

      <transition name="slide">
        <div class="songList lb" v-show="listShow">
          <BFlist
            v-for="songitm of songlist"
            :key="songitm.id"
            :item="songitm"
            @Playlistmuc="$emit('Playlistmuc', $event)"
            @delsong="$emit('delsong', $event)"
            :playing="playing"
            :AudioplaysongId="AudioplaysongId"
          >
          </BFlist>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import BFlist from "@/components/BFlist.vue";

export default {
  components: {
    BFlist,
  },

  props: {
    songXiangqing: Object,
    AudioplaysongId: Number,
    playing: Boolean,
    songoverTime: Number,
    songoldTime: Number,
    songlist: Array,
    Audioplaysong: Object,
  },
  data() {
    return {
      jindu: null,
      geci: null,
      dbshow: true,
      song: null,
      userValue: 0,
      gecitop: 0,
      songlyrics: [],
      xuanzhon: true,
      timeroutId: null,
      titleText: "分享给好友？",
      fenxiangshow: false,
      listShow: false,

      playsonglyeis: null,
    };
  },
  created() {
    this.songUpdata();
    // this.$emit('Playlistmuc', $event)
  },
  computed: {
    // 测试
    songlyricIndex: function () {
      // console.log(10, this.geci);
      return this.geci?.findLastIndex((item) => {
        return item.time <= this.songoldTime;
      });

      //   return this.currentTime;
    },

    // gecitext() {
    //   console.log(this.gecitop);

    //   return this.geci[this.gecitop]
    // }
  },
  methods: {
    // 切换歌词和专辑
    qie() {
      this.dbshow = !this.dbshow;
      this.$nextTick(() => {
        console.log("歌词列表", this.$refs.lyricElm);

        this.songlyrics = this.$refs.lyricElm.map((ele) => {
          console.log(ele.offsetHeight);

          return ele.offsetHeight;
        });
      });
    },
    // 返回

    back() {
      if (this.dbshow) {
        this.$router.back();
      } else {
        this.dbshow = true;
      }
    },
    // 切换列表显隐
    liebiaoqiehuan() {
      this.listShow = !this.listShow;
    },

    // 测试
    ctrlC() {
      async function writeToClipboard(text) {
        try {
          await navigator.clipboard.writeText(text);
          console.log("内容已复制到剪贴板");
        } catch (err) {
          console.error("无法复制内容到剪贴板: ", err);
        }
      }

      writeToClipboard(
        `http://localhost:8080/#/bofangqi?id=${this.AudioplaysongId}`
      );

      this.titleText = "链接复制已经复制至粘贴板！";

      setTimeout(() => {
        this.titleText = "分享给好友？";
      }, 2000);
    },

    // 更改
    genggai(time) {
      console.log(time);
      this.$emit("changeSongTime", time);
    },

    anxia() {
      this.xuanzhon = false;
      if (this.timeroutId) {
        console.log("清除", this.timeroutId);
        clearTimeout(this.timeroutId);
        this.timeroutId = null;
      }

      // console.log('滑动');
    },
    sonkai() {
      this.timeroutId = setTimeout(() => {
        this.xuanzhon = true;
        console.log("延时器执行");
      }, 5000);
    },

    // 获取内容
    songUpdata() {
      this.axios
        .get(`/lyric?id=${this.AudioplaysongId || this.$route.query.id}`)
        .then((res) => {
          let songarr = res.data.lrc.lyric;
          // console.log("获取", songarr);

          let songGeci = songarr?.split("\n")?.filter((item) => item);

          // console.log(songGeci);
          this.jindu = [...songGeci];
          var parsedLyric = songGeci?.map((item, index) => {
            var res = item.match(
              /\[(?<m>\d{2}):(?<s>\d{2}\.\d{2,3})\](?<str>.*)/i
            );
            return {
              time: res ? res.groups.m * 60 + parseFloat(res.groups.s) : null,
              text: res ? res.groups.str.trim() : "Invalid lyric format",
              id: index,
            };
          });

          let ssrarr = parsedLyric.filter((item) => {
            return item.text != "";
          });
          // console.log(ssrarr);
          this.geci = [...ssrarr];

          // console.log("歌词", this.geci, "进度", this.jindu);

          // songGeci.match(/\[(?<m>\d{2}):(?<s>\d{2}\.\d{2,3})\](?<str>.*)/i)
        }),
        this.axios
          .get("/song/detail", {
            params: {
              ids: this.AudioplaysongId || this.$route.query.id,
            },
          })
          .then((res) => {
            this.song = res.data.songs[0];
            console.log("歌曲", this.song);

            let imgs = `url("${res.data.songs[0]?.al?.picUrl}")`;
            console.log("图片", imgs);

            this.$emit("playAudioId", this.song);

            this.$refs.htmlbacc.style.backgroundImage = imgs;
          });
    },
  },

  watch: {
    gecitop(newV) {
      this.$refs.gecibox.scrollTop = newV;
      // console.log(this.geci[0]);
      // console.log(this.songlyricIndex);
    },
    songlyricIndex(newindex) {
      // this.$emit('setsonglyir', this.geci[newindex].text)
      console.log(this.geci[newindex]?.text);

      this.playsonglyeis = this.geci[newindex]?.text;
    },

    AudioplaysongId(olaV,newV) {
      console.log(newV);
      this.axios
        .get("/song/detail", {
          params: {
            ids: this.AudioplaysongId || newV,
          },
        })
        .then((res) => {
          this.song = res.data.songs[0];
          // this.$nextTick(() => {
          let imgs = `url("${res.data.songs[0]?.al?.picUrl}")`;
          console.log("图片", imgs);

          this.$refs.htmlbacc.style.backgroundImage = imgs;
          // })

          console.log("歌曲", this.song);
          if (olaV != newV ) {
            this.$router.push({ path: `/bofangqi?id=${newV}` });
          }

          
        });

      this.axios.get(`/lyric?id=${newV}`).then((res) => {
        let songarr = res.data.lrc?.lyric;
        console.log("获取", songarr);

        let songGeci = songarr?.split("\n")?.filter((item) => item);

        console.log(songGeci);
        this.jindu = [...songGeci];
        var parsedLyric = songGeci?.map((item, index) => {
          var res = item.match(
            /\[(?<m>\d{2}):(?<s>\d{2}\.\d{2,3})\](?<str>.*)/i
          );
          return {
            time: res ? res.groups.m * 60 + parseFloat(res.groups.s) : null,
            text: res ? res.groups.str.trim() : "Invalid lyric format",
            id: index,
          };
        });

        let ssrarr = parsedLyric.filter((item) => {
          return item.text != "";
        });
        console.log(ssrarr);
        this.geci = [...ssrarr];

        // songGeci.match(/\[(?<m>\d{2}):(?<s>\d{2}\.\d{2,3})\](?<str>.*)/i)

        this.$nextTick(() => {
          console.log("歌词列表", this.$refs.lyricElm);

          this.songlyrics = this.$refs.lyricElm.map((ele) => ele.offsetHeight);
        });
      });
    },

    songoldTime: function (songoldTime) {
      if (!this.inputing) {
        this.userValue = songoldTime;
      }
      if (this.xuanzhon) {
        this.gecitop =
          this.songlyrics
            .slice(0, this.songlyricIndex)
            .reduce((total, num) => total + num, 0) - 200;
      }
    },
  },
};
</script>

